import React from 'react'

function HeroSec() {
  return (
    <section className="heroSection wf-section">
    
    <div className="hero-container">
        <div className="container">
            <h1 className="heading-primary">Claim Your Shirt</h1>
            <p className="hero-text">All-in-one place for your remote team to chat, collaborate and track project progress.</p>
            <div className="hero-form w-form">
                <form id="email-form" name="email-form" data-name="Email Form" method="get" className="form-block" aria-label="Email Form">
                    <input type="email" className="text-field idd w-input" autofocus="true" maxlength="256" name="email" data-name="Email" placeholder="Enter Your Employe ID" id="email" required="" />
                    <input type="submit" value="Claim Now!" data-wait="Please wait..." className="btnn" />
                </form>
                <div className="success-message w-form-done " tabindex="-1" role="region" aria-label="Email Form success">
                    <div className="text-block">Thank you! Your submission has been received!</div>
                </div>
                <div className="error-message w-form-fail" tabindex="-1" role="region" aria-label="Email Form failure">
                    <div className="text-block-2">Oops! Something went wrong while submitting the form.</div>
                </div>
            </div>
        </div>
    </div>
</section>
  )
}

export default HeroSec